import wiki from './wiki';

wiki()
	.random(10)
	.then(pages => Promise.all(pages.map(name => wiki().page(name))))
	.then(pages => Promise.all(pages.map(page => {
		return page.info().then(info => {
			return info;
		}).catch(e => {
			console.log('Issue parsing info', e);
			console.log(JSON.stringify(page.raw, null, 3));
		});
	})))
	.then(infos => console.log(JSON.stringify(infos, null, 3)))
	.catch(e => console.error(e));
